<template id="myBoard">
  <v-card flat>
    <v-card-title primary-title>
      <v-layout column>
        <v-layout row wrap>
          <v-flex xs12 sm6 lg5 v-if="lounges.length > 0" :class="{'mt-5':$vuetify.breakpoint.xsOnly, 'pr-2':$vuetify.breakpoint.mdAndUp, 'ml-auto':true}">
            <v-layout row class="titleRow">
              <span class="title"></span>
            </v-layout>
            <span class="ellipsis subtitle">  /   .</span>
            <v-divider class="my-3"/>
            <v-list dense>
              <v-list-tile v-for="lounge in lounges" :key="lounge.boardId">
                <v-layout row>
                  <v-flex class="ellipsis">
                    <div class="vertical-center ellipsis">
                      {{lounge.boardName}}
                      <v-icon v-if="lounge.isOwner" title="">person</v-icon>
                    </div>
                  </v-flex>
                  <v-spacer/>
                  <v-tooltip v-if="lounge.writeRestrictDate" bottom>
                    <v-chip slot="activator" color="red" text-color="white" small></v-chip>
                    <span>{{$moment(lounge.writeRestrictDate, 'YYYYMMDD').format('Y M D')}}</span>
                  </v-tooltip>
                  <v-tooltip v-if="lounge.readRestrictDate" bottom>
                    <v-chip slot="activator" color="red" text-color="white" small></v-chip>
                    <span>{{$moment(lounge.readRestrictDate, 'YYYYMMDD').format('Y M D')}}</span>
                  </v-tooltip>
                </v-layout>
              </v-list-tile>
            </v-list>
          </v-flex>
          <v-flex xs12 :sm6="lounges.length > 0" :sm9="lounges.length === 0" :lg5="lounges.length > 0" :lg8="lounges.length === 0" :class="{'pl-2':$vuetify.breakpoint.mdAndUp, 'mr-auto':true, 'ml-auto':lounges.length === 0}">
            <v-layout row :class="{'titleRow':true, 'mb-2':lounges.length === 0}">
              <span :class="{'title':lounges.length > 0, 'headline':lounges.length===0}"></span>
              <v-spacer/>
              <v-btn id="createTopic" small @click="openDialog(null)" color="accent" :class="{'my-0':true, 'ml-0':true, 'mr-0':$vuetify.breakpoint.xsOnly}" title="    ."></v-btn>
              <br>
            </v-layout>
            <span class="ellipsis subtitle">    .</span>
            <v-divider class="my-3"/>
            <draggable v-model="topics" v-if="topics.length > 0">
              <transition-group>
                <v-layout class="cursor-move align-center" row v-for="(topic, index) in topics" :key="topic.boardId">
                  <v-flex class="ellipsis align-center">
                    <div class="align-center ellipsis">
                      {{topic.boardName}}
                      <v-icon v-if="topic.isOwner" class="vertical-align-middle" title="">person</v-icon>
                    </div>
                  </v-flex>
                  <v-spacer/>
                  <template v-if="!topic.readRestrictDate && !topic.writeRestrictDate">
                    <v-btn flat v-if="!topic.isOwner" primary small class="mx-0" @click="removeItem(index)"></v-btn>
                    <v-btn flat v-else primary small class="mx-0" @click="openDialog(topic)" title="     ."></v-btn>
                  </template>
                  <template v-else>
                    <v-tooltip v-if="topic.writeRestrictDate" bottom>
                      <v-chip slot="activator" color="red" text-color="white" small></v-chip>
                      <span>{{$moment(topic.writeRestrictDate, 'YYYYMMDD').format('Y M D')}}</span>
                    </v-tooltip>
                    <v-tooltip v-if="topic.readRestrictDate" bottom>
                      <v-chip slot="activator" color="red" text-color="white" small></v-chip>
                      <span>{{$moment(topic.readRestrictDate, 'YYYYMMDD').format('Y M D')}}</span>
                    </v-tooltip>
                  </template>
                </v-layout>
              </transition-group>
            </draggable>
            <v-layout v-else>
              <span>    .
                <br>
                <router-link to="/searchBoard" class="primary--text"> </router-link> !
              </span>
            </v-layout>
          </v-flex>
        </v-layout>
      </v-layout>
      <v-dialog v-model="dialog" id="topicCreatorDialog" :fullscreen="$vuetify.breakpoint.xsOnly" :transition="$vuetify.breakpoint.xsOnly?'dialog-bottom-transition':'fade-transition'" lazy scrollable max-width="700px">
        <topic-creator @closeDialog="closeDialog" @resetBoard="resetBoard" :board="editItem"/>
      </v-dialog>
    </v-card-title>
    <v-card-actions pa-3>
      <v-layout :row="$vuetify.breakpoint.smAndUp" :column="$vuetify.breakpoint.xsOnly" wrap text-xs-right>
        <v-btn @click="reset" class="mt-2" flat></v-btn>
        <v-spacer></v-spacer>
        <v-btn @click="save" color="primary" :loading="loading" class="mt-2"></v-btn>
      </v-layout>
    </v-card-actions>
  </v-card>
</template>
<script>
import draggable from 'vuedraggable';
import TopicCreator from './TopicCreator';
export default {
  name: 'MyBoard',
  components: {
    draggable,
    TopicCreator
  },
  data () {
    return {
      loading: false,
      dialog: false,
      topics: [],
      editItem: {}
    };
  },
  methods: {
    reset () {
      this.topics = JSON.parse(JSON.stringify(this.originalTopics));
    },
    isDirty () {
      if (this.topics.length !== this.originalTopics.length) {
        return true;
      }
      let i = 0;
      while (i < this.topics.length) {
        if (this.topics[i].boardId !== this.originalTopics[i].boardId) {
          return true;
        }
        i++;
      }
      return false;
    },
    save () {
      if (!this.isDirty()) {
        this.$store.dispatch('showSnackbar', { text: '  .', color: 'info' });
        return;
      }
      this.loading = true;
      this.$axios
        .put('/user/board', { boards: this.topics.map((item, index) => ({ orderNumber: index + 1, boardId: item.boardId })) })
        .then(responose => {
          this.loading = false;
          this.$store.dispatch('setUserBoards', JSON.parse(JSON.stringify(this.topics)).concat(this.userBoards.filter(x => x.boardType !== 'T')));
          this.$store.dispatch('showSnackbar', { text: '  .', color: 'success' });
        })
        .catch(error => {
          console.log(error);
          this.loading = false;
          this.$store.dispatch('showSnackbar', { text: error.response ? error.response.data.message || '   .' : '  .', color: 'error' });
        });
    },
    removeItem (index) {
      const topic = this.topics[index];
      if (topic) {
        this.$axios
          .delete('/user/board/' + topic.boardId)
          .then(response => {
            this.$store.dispatch('removeUserBoard', topic.boardId);
            this.topics.splice(index, 1);
            this.$store.dispatch('showSnackbar', { text: '  .', color: 'success' });
          })
          .catch(error => {
            console.log(error.response);
            this.$store.dispatch('showSnackbar', { text: error.response ? error.response.data.message || '   .' : '   .', color: 'error' });
          });
      }
    },
    openDialog (item) {
      if (item === null) {
        if (this.$store.getters.profile.auth !== 'A') {
          this.$store.dispatch('showSnackbar', { text: '      .', color: 'error' });
          return;
        }
        this.editItem = null;
        this.dialog = true;
      } else if (item) {
        this.$axios
          .get('/board', { params: { boardId: item.boardId } })
          .then(response => {
            this.editItem = response.data;
            this.dialog = true;
          })
          .catch(error => {
            console.log(error.response);
            this.$store.dispatch('showSnackbar', { text: error.response ? error.response.data.message || '   .' : '   .', color: 'error' });
          });
      }
    },
    closeDialog () {
      this.dialog = false;
    },
    resetBoard () {
      this.$axios
        .get('/user/board')
        .then(response => {
          this.$store.dispatch('setUserBoards', response.data);
          this.$nextTick(this.reset);
        })
        .catch(error => {
          console.log(error);
          this.$store.dispatch('showSnackbar', { text: error.response ? error.response.data.message || '    .' : '    .', color: 'error' });
        });
    }
  },
  computed: {
    originalTopics () {
      return this.userBoards
        .filter(x => x.boardType === 'T')
        .map(x => {
          if (!x.readRestrictDate || !this.$moment(x.readRestrictDate, 'YYYYMMDD').isValid() || this.$moment(x.readRestrictDate, 'YYYYMMDD').isBefore(this.$moment())) {
            delete x.readRestrictDate;
          }
          if (!x.writeRestrictDate || !this.$moment(x.writeRestrictDate, 'YYYYMMDD').isValid() || this.$moment(x.writeRestrictDate, 'YYYYMMDD').isBefore(this.$moment())) {
            delete x.writeRestrictDate;
          }
          return x;
        });
    },
    lounges () {
      return this.userBoards
        .map(x => {
          if (!x.readRestrictDate || !this.$moment(x.readRestrictDate, 'YYYYMMDD').isValid() || this.$moment(x.readRestrictDate, 'YYYYMMDD').isBefore(this.$moment())) {
            delete x.readRestrictDate;
          }
          if (!x.writeRestrictDate || !this.$moment(x.writeRestrictDate, 'YYYYMMDD').isValid() || this.$moment(x.writeRestrictDate, 'YYYYMMDD').isBefore(this.$moment())) {
            delete x.writeRestrictDate;
          }
          return x;
        })
        .filter(x => x.boardType !== 'T' && (x.writeRestrictDate || x.readRestrictDate));
    },
    userBoards () {
      return this.$store.getters.userBoards;
    }
  },
  mounted () {
    this.reset();
  },
  watch: {
    userBoards () {
      this.reset();
    },
    dialog (val) {
      document.body.style.position = val ? 'fixed' : 'initial';
    }
  },
  render (h) {
    return h('myBoard', { attrs: { id: 'app' } }, this.draggable);
  }
};
</script>
<style>
.cursor-move {
  height: 40px;
}
.subtitle {
  display: block;
}
.v-tooltip .v-chip {
  margin-top: 8px;
  margin-bottom: 8px;
}
.titleRow {
  height: 24px;
}
#createTopic {
  top: -4px;
}
#topicCreatorDialog {
  -webkit-overflow-scrolling: touch;
}
</style>
