<template>
  <v-card tile>
    <v-toolbar card color="white">
      <v-toolbar-title>{{board?' ':'  '}}</v-toolbar-title>
      <v-spacer></v-spacer>
      <v-toolbar-items>
        <v-btn icon @click="$emit('closeDialog', null)">
          <v-icon>close</v-icon>
        </v-btn>
      </v-toolbar-items>
    </v-toolbar>
    <v-card-text>
      <v-form ref="form">
        <v-layout row xs12 wrap>
          <v-flex xs6 class="mb-1">
            <v-text-field v-if="board" v-model="boardId" class="dense" readonly label="ID" hint="pedagy.com/<b>seoul</b>  URL  ."></v-text-field>
            <v-text-field v-else v-model="boardId" :rules="boardIdRules" class="dense" :error-messages="boardIdErrors" maxlength="15" label="ID" hint="pedagy.com/<b>seoul</b>  URL  ." placeholder="  ." validate-on-blur @blur="checkBoardId"></v-text-field>
          </v-flex>
          <v-flex xs6 class="mb-1">
            <v-text-field v-model="boardName" :rules="boardNameRules" class="dense" maxlength="200" label=" " required hint="   ." validate-on-blur></v-text-field>
          </v-flex>
          <v-flex xs12>
            <v-textarea ref="boardDescription" v-model="boardDescription" :rows="3" maxlength="1000" height="80px" label=" " hint="     ." validate-on-blur></v-textarea>
          </v-flex>
          <v-flex xs6>
            <v-checkbox ref="allowAnonymous" v-model="allowAnonymous" class="dense mt-2" label=" " hide-details></v-checkbox>
          </v-flex>
          <v-flex xs6>
            <v-tooltip bottom>
              <v-checkbox ref="useCategory" slot="activator" v-model="useCategory" class="dense mt-2" label="  " hide-details></v-checkbox>
              <span>    //      .</span>
            </v-tooltip>
          </v-flex>
          <v-flex xs6 class="mt-3">
            <v-select v-model="allGroupAuth" :items="allGroupAuthItems" label="  " dense></v-select>
          </v-flex>
          <v-flex xs6 class="mt-3">
            <v-layout align-center fill-height>
              <component :is="$vuetify.breakpoint.xsOnly?'small':'div'" v-html="allGroupAuthDescription"></component>
            </v-layout>
          </v-flex>
          <v-flex xs12>
            <component :is="$vuetify.breakpoint.xsOnly?'v-select':'v-autocomplete'" name="allowedGroups" chips multiple item-text="text" dense item-value="value" v-model="allowedGroups" :disabled="allGroupAuth === 'READWRITE'" :items="groupItems" label=" " :hint="'     .' + (myGroup !== ''? '    : ' + myGroup : '')" persistent-hint :menu-props="{closeOnContentClick:$vuetify.breakpoint.xsOnly}">
              <template slot="selection" slot-scope="props">
                <v-chip close small :key="props.item.value" :selected="props.selected" @input="removeChip(props, props.item, allowedGroups)">{{props.item.text}}</v-chip>
              </template>
              <template slot="item" slot-scope="data">
                <v-list-tile-content>
                  <v-list-tile-title v-html="data.item.text"></v-list-tile-title>
                </v-list-tile-content>
              </template>
            </component>
          </v-flex>
          <v-flex xs12 v-if="board" class="mt-2">
            <v-select name="ownerNickName" dense v-model="ownerNickName" :items="boardMemberItems" label=" " hint="   ." persistent-hint></v-select>
          </v-flex>
          <v-flex xs12 v-if="board && board.reservedDate" class="mt-3">
            <v-textarea ref="reservedContents" v-model="reservedContents" :rows="3" readonly height="80px" :label="' ('+$moment(board.reservedDate, 'YYYYMMDD').format('Y.M.D')+'  ) '" hint="   ."></v-textarea>
          </v-flex>
        </v-layout>
        <v-divider class="my-2"/>
        <v-layout row>
          <v-btn flat @click="reset(true)"></v-btn>
          <v-btn color="error" @click="deleteBoard" v-if="board"></v-btn>
          <v-spacer/>
          <v-btn color="primary" @click="save" :loading="loading"></v-btn>
        </v-layout>
      </v-form>
    </v-card-text>
  </v-card>
</template>
<script>
const reserved = ['document', 'documents', 'profile', 'profiles', 'auth', 'user', 'users', 'comment', 'comments', 'vote', 'votes', 'report', 'reports', 'index', 'scraps', 'scrap', 'board', 'boards', 'manage', 'manages', 'chat', 'chats', 'message', 'messages', 'group', 'groups', 'event', 'events', 'signup', 'signin', 'signout', 'resetPassword', 'notification', 'notifications', 'survey', 'list', 'admin', 'ADMIN', 'ADMINISTRATOR', 'administrator', 'attach', 'profiles', 'animal', 'loungeBest', 'topicBest', 'lounge', 'topic', 'type', 'best', 'sanction', 'userId', 'nickName', 'myPage', 'myBoard', 'myCommunity', 'pedagy', 'myPedagy', 'Pedagy'];
const boardIdRegex = [/^(?:[a-zA-Z]+)(?:[a-zA-Z0-9\-_]{0,15})$/, /^((?!(--|__)).)*$/];
const groupName = { M: '', G: '', N: '', R: '', Z: '' };
export default {
  name: 'topicCreator',
  data () {
    return {
      loading: false,
      boardId: null,
      boardName: null,
      boardDescription: null,
      allGroupAuth: 'READONLY',
      allowAnonymous: true,
      useCategory: false,
      allowedGroups: [],
      allGroupAuthItems: [{ value: 'READWRITE', text: '' }, { value: 'READONLY', text: '' }, { value: 'NONE', text: '' }],
      ownerNickName: null,
      groupItems: [],
      boardIdRules: [v => !reserved.includes(v) || '   ID.', v => !v || (v.length > 3 && v.length < 16) || '4~15 .', v => boardIdRegex[0].test(v) || 'ID   ,    .', v => boardIdRegex[1].test(v) || 'ID  [_, -] .'],
      boardNameRules: [v => !!v || '  .'],
      boardMemberItems: [],
      boardIdErrors: []
    };
  },
  props: ['board'],
  computed: {
    myGroup () {
      const profile = this.$store.getters.profile;
      return this.groupItems
        .filter(x => profile.groups.some(y => y === x.value))
        .map(x => x.text)
        .join(', ');
    },
    allGroupAuthDescription () {
      switch (this.allGroupAuth) {
        case 'READONLY':
          return '    ,<br>     .<br>    .';
        case 'NONE':
          return '     .<br>   .';
        case 'READWRITE':
          return '     .<br>     .<br>  .';
      }
    },
    reservedContents () {
      if (this.board && this.board.reservedContents) {
        let reservedContents = this.board.reservedContents;
        let string = '';
        for (let key in reservedContents) {
          switch (key) {
            case 'boardName':
              string += `  : ${reservedContents[key]}\n`;
              break;
            case 'boardDescription':
              string += `  : ${reservedContents[key]}\n`;
              break;
            case 'allowAnonymous':
              string += `   : ${reservedContents[key] ? '' : ''}\n`;
              break;
            case 'useCategory':
              string += ` : ${reservedContents[key] ? '' : ''}\n`;
              break;
            case 'allGroupAuth':
              string += ` / : ${this.allGroupAuthItems.some(x => x.value === reservedContents[key]) ? this.allGroupAuthItems.find(x => x.value === reservedContents[key]).text : '(  )'}\n`;
              break;
            case 'ownerNickName':
              string += ` : ${reservedContents[key]}\n`;
              break;
            case 'auth':
              string += `  : ${reservedContents[key]
                .filter(x => this.groupItems.some(y => y.value === x.groupId))
                .map(x => this.groupItems.find(y => y.value === x.groupId).text + (x.command === 'INSERT' ? ' ' : x.command === 'DELETE' ? ' ' : ''))
                .join(', ')}`;
              break;
            case 'status':
              string += reservedContents[key] === 'DELETED' ? '  ' : '';
              break;
          }
        }
        return string;
      } else {
        return null;
      }
    }
  },
  methods: {
    reset (skipGetInformationFormServer) {
      if (this.board) {
        if (!skipGetInformationFormServer) {
          this.loading = true;
          this.$axios
            .get('/board/member', { params: { boardId: this.board.boardId }, headers: { silent: true } })
            .then(response => {
              const myNickName = this.$store.getters.profile.topicNickName;
              this.boardMemberItems = response.data.filter(x => x.nickName !== myNickName).map(x => x.nickName);
              this.loading = false;
            })
            .catch(error => {
              console.log(error.response);
              this.loading = false;
              this.$store.dispatch('showSnackbar', { text: error.response ? error.response.data.message : '   .', color: 'error' });
            });
        }
        this.boardId = this.board.boardId;
        this.boardName = this.board.boardName;
        this.boardDescription = this.board.boardDescription;
        this.allGroupAuth = this.board.allGroupAuth;
        this.allowAnonymous = this.board.allowAnonymous;
        this.useCategory = this.board.categories ? this.board.categories.filter(x => x).length > 0 : false;
        this.allowedGroups = this.board.boardAuth.map(x => x.groupId);
        this.ownerNickName = this.board.ownerNickName;
      } else {
        this.boardId = null;
        this.boardName = null;
        this.boardDescription = null;
        this.allGroupAuth = 'READONLY';
        this.allowAnonymous = true;
        this.useCategory = false;
        this.allowedGroups = [];
        this.ownerNickName = null;
      }
    },
    removeChip (props, item, list) {
      props.parent.selectedItems.splice(props.parent.selectedItems.indexOf(item.value), 1);
      list.splice(list.indexOf(item.value), 1);
    },
    checkBoardId (event) {
      if (this.boardId && this.boardIdErrors.length === 0) {
        this.$axios
          .get('/boardId', { params: { boardId: this.boardId }, headers: { silent: true } })
          .then(response => {
            this.boardIdErrors = [];
            if (typeof event === 'function') {
              event.call(this);
            }
          })
          .catch(error => {
            this.boardIdErrors = [error && error.response && error.response.data.message];
            if (typeof event === 'function') {
              event.call(this);
            }
          });
      } else if (typeof event === 'function') {
        event.call(this);
      }
    },
    save () {
      if (this.board) {
        if (this.$refs.form.validate() && this.boardIdErrors.length === 0) {
          if (confirm('  1  . ?')) {
            this.loading = true;
            this.$axios
              .put('/board', {
                boardId: this.boardId,
                boardName: this.boardName,
                boardDescription: this.boardDescription || undefined,
                allGroupAuth: this.allGroupAuth,
                allowAnonymous: this.allowAnonymous,
                allowedGroups: this.allowedGroups.map(x => ({ groupId: x, authType: 'READWRITE' })),
                useCategory: this.useCategory,
                ownerNickName: this.ownerNickName || undefined
              })
              .then(response => {
                this.loading = false;
                this.$emit('resetBoard');
                this.$emit('closeDialog');
                this.$store.dispatch('showSnackbar', { text: '   .', color: 'success' });
              })
              .catch(error => {
                console.log(error.response);
                this.loading = false;
                this.$store.dispatch('showSnackbar', { text: error.response ? error.response.data.message : '  .', color: 'error' });
              });
          }
        } else {
          this.$store.dispatch('showSnackbar', { text: '   .', color: 'error' });
        }
      } else {
        this.checkBoardId(() => {
          if (this.$refs.form.validate() && this.boardIdErrors.length === 0) {
            this.loading = true;
            this.$axios
              .post('/board', {
                boardId: this.boardId,
                boardName: this.boardName,
                boardType: 'T',
                boardDescription: this.boardDescription || undefined,
                allGroupAuth: this.allGroupAuth,
                allowAnonymous: this.allowAnonymous,
                allowedGroups: this.allowedGroups.map(x => ({ groupId: x, authType: 'READWRITE' })),
                useCategory: this.useCategory
              })
              .then(response => {
                this.loading = false;
                this.$store.dispatch('addBoard', {
                  boardId: this.boardId,
                  boardName: this.boardName,
                  boardType: 'T',
                  boardDescription: this.boardDescription || undefined,
                  allGroupAuth: this.allGroupAuth,
                  allowAnonymous: this.allowAnonymous
                });
                this.$store.dispatch('addUserBoard', {
                  boardId: this.boardId,
                  boardName: this.boardName,
                  boardType: 'T',
                  boardDescription: this.boardDescription || undefined,
                  allGroupAuth: this.allGroupAuth,
                  allowAnonymous: this.allowAnonymous
                });
                this.$emit('resetBoard');
                this.$emit('closeDialog');
                this.reset();
                this.$store.dispatch('showSnackbar', { text: '  .', color: 'success' });
              })
              .catch(error => {
                console.log(error.response);
                this.loading = false;
                this.$store.dispatch('showSnackbar', { text: error.response ? error.response.data.message : '  .', color: 'error' });
              });
          } else {
            this.$store.dispatch('showSnackbar', { text: '   .', color: 'error' });
          }
        });
      }
    },
    deleteBoard () {
      if (confirm('  1   ,\n     . ?')) {
        this.$axios
          .put('/board', { boardId: this.boardId, status: 'DELETED' })
          .then(response => {
            this.$emit('resetBoard');
            this.$emit('closeDialog');
            this.$store.dispatch('showSnackbar', { text: '  .', color: 'success' });
          })
          .catch(error => {
            console.log(error.response);
            this.$store.dispatch('showSnackbar', { text: error.response ? error.response.data.message : '  .', color: 'error' });
          });
      }
    }
  },
  created () {
    if (this.$store.getters.groups) {
      this.groupItems = this.$store.getters.groups;
    } else {
      this.$axios
        .get('/group', { headers: { silent: true } })
        .then(response => {
          response.data.forEach(x => {
            if (x.groupType === 'N' || x.groupType === 'D' || x.groupType === 'E') {
              x.groupType = 'Z';
            }
          });
          this.groupItems = response.data.sort((a, b) => (a.groupType < b.groupType ? -1 : a.groupType === b.groupType ? 0 : 1));
          let previous = null;
          let i = 0;
          while (i < this.groupItems.length) {
            if (previous !== this.groupItems[i].groupType) {
              if (previous) {
                previous = this.groupItems[i].groupType;
                this.groupItems.splice(i, 0, { divider: true });
                i++;
              } else {
                previous = this.groupItems[i].groupType;
              }
              this.groupItems.splice(i, 0, { header: groupName[previous] });
              i++;
            }
            i++;
          }
          this.groupItems = this.groupItems.map(x => (x.groupName ? { text: x.groupName, value: x.groupId } : x));
          this.$store.dispatch('setGroups', this.groupItems);
        })
        .catch(error => {
          this.$store.dispatch('showSnackbar', { text: `   .${error && error.response && error.response.data ? '[' + error.response.data.message + ']' : ''}` });
        });
    }
    this.reset();
  },
  watch: {
    boardId () {
      if (this.boardIdErrors.length > 0) {
        this.boardIdErrors = [];
      }
    },
    board: {
      handler () {
        this.reset();
      },
      deep: true
    }
  }
};
</script>
